<template>
  <div>
    <v-row>
        <v-col cols="12">
      <v-data-table dense class="mt-4 entriesTable elevation-2"
      :items="kvTable" :headers="kvTableHeaders" caption="Contract State"
      :items-per-page="100"
      :server-items-length="rowCount"
      >
      </v-data-table>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeStartTime" label="Bull's Eye Start Time"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeEndTime" label="Bull's Eye End Time"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeOracle" label="Bull's Eye Oracle"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-btn @click="createBullsEyeEvent()">Create Event</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeStartTime" label="Bull's Eye Start Time"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeEndTime" label="Bull's Eye End Time"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeOracle" label="Bull's Eye Oracle"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-btn @click="getEvent()">Get Event</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeStartTime" label="Bull's Eye Start Time"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeEndTime" label="Bull's Eye End Time"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeOracle" label="Bull's Eye Oracle"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-btn @click="makePrediction()">Make Prediction</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeStartTime" label="Bull's Eye Start Time"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeEndTime" label="Bull's Eye End Time"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-text-field v-model="bullsEyeOracle" label="Bull's Eye Oracle"></v-text-field>
      </v-col>
      <v-col cols="3">
        <v-btn @click="resolveEvent()">Resolve Event</v-btn>
      </v-col>
    </v-row>
    
  </div>
</template>

<script>
import BigNumber from 'bignumber.js';
import BullsEye from "../lib/contracts/bullsEye";
import {mapGetters, mapMutations} from 'vuex';
import moment from "moment";
import {EventBus} from '../store/eventBus';

export default {
  name: 'BullsEyeAdmin',
  data: function(){
    return {
      client: {},
      kvTable: [],
      kvTableHeaders: [{text: 'Key', value: 'key', sortable: false}, {text: 'Val', value: 'val', sortable: false}],
      rowCount: 100,
      instrument: null,
      bullsEyeStartTime: '',
      bullsEyeEndTime: '',
      bullsEyeOracle: ''
    }
  },
  mounted: async function(){
    await this.init();
  },
  destroyed: async function(){

  },
  computed: {
    ...mapGetters([
        "network",
        "getSelectedInstrument",
        "account",
        "loading",
        "balance"
    ]),
    
  },
  methods: {
    fetch: async function(){
      this.kvTable = [];
      const owner = await this.client.signerContract.owner()
      this.kvTable.push({key: "Owner", val: owner});
      this.kvTable.push({key: "contractAddress", val: this.client.contractAddress});
      
    },
    init: async function(){
      this.instrument = this.network.instrument[this.getSelectedInstrument];
      this.client = new BullsEye({
        network: this.network,
        instrument: this.getSelectedInstrument,
      });
      await this.client.init();
      this.bullsEyeOracle = this.client.network.instrument[this.client.instrument].oracle;
      const now = new Date().getTime();
      this.bullsEyeStartTime = (now - now % 1000)/1000;
      this.bullsEyeEndTime = this.bullsEyeStartTime + 2*60;
      this.fetch();

    },
    createBullsEyeEvent: async function(){
      console.log(this.bullsEyeStartTime, this.bullsEyeEndTime, this.bullsEyeOracle);
      const bands = [10000, 20000, 40000, 80000];
      const payouts = [4800, 2400, 1200, 600];
      const createCommissionBasisPoints = 0;
      const resolution = 1;
      const realMoney = false;
      const withSigner = this.client.signerContract.connect(this.client.metamaskProvider.getSigner());
      console.log("createEvent", this.bullsEyeStartTime, this.bullsEyeEndTime, 'Event-' + this.bullsEyeStartTime, this.bullsEyeOracle);
      await withSigner.createEvent(this.bullsEyeStartTime, this.bullsEyeEndTime, 'Event-' + this.bullsEyeStartTime, 'Test Game', bands, payouts, createCommissionBasisPoints, resolution, this.bullsEyeOracle,realMoney, {gasLimit: 500000});
    },
    getEvent: async function(){
      const eventID = await this.client.signerContract.getEventID(this.bullsEyeStartTime, this.bullsEyeEndTime, 'Event-' + this.bullsEyeStartTime, this.bullsEyeOracle);
      console.log("eventID", eventID);
      const event = await this.client.signerContract.bullsEyeEvents(eventID);
      console.log("event", event);
      console.log("isResolved", event.resolved);
    },
    makePrediction: async function(){
      const eventID = await this.client.signerContract.getEventID(this.bullsEyeStartTime, this.bullsEyeEndTime, 'Event-' + this.bullsEyeStartTime, this.bullsEyeOracle);
      console.log("eventID", eventID);
      const withSigner = this.client.signerContract.connect(this.client.metamaskProvider.getSigner());
      await withSigner.makePrediction(eventID, 0, 100, {gasLimit: 500000});
    },
    resolveEvent: async function(){
      const eventID = await this.client.signerContract.getEventID(this.bullsEyeStartTime, this.bullsEyeEndTime, 'Event-' + this.bullsEyeStartTime, this.bullsEyeOracle);
      console.log("resolve eventID", eventID);
      const withSigner = this.client.signerContract.connect(this.client.metamaskProvider.getSigner());
      const outcomeValue = 100;
      const distributePlayerWins = false;
      await withSigner.resolveEvent(eventID, outcomeValue, distributePlayerWins, {gasLimit: 1000000});
      //await withSigner.resolveOracleEvent(eventID, false, 0, {gasLimit: 1000000});
    }
  }
}
</script>

